const app = getApp()
var QQMapWX = require('./qqmap-wx-jssdk.min.js')
var qqmapsdk = new QQMapWX({
  key: 'LXCBZ-NNIKD-UZ64F-H6AFI-UNJLH-OCFGE'
})
export default {
	props: {
		tipText: {
			type: String,
			default: '选择位置'
		},
		descText: {
			type: String,
			default: '使用当前定位或在地图上标记位置'
		},
	},
	data() {
		return {
			mapH: 0,             // 地图高度，可在initMapH()中设置高度
			longitude: 0,        // 初始经度
			latitude: 0,         // 初始纬度
			myAddress: '',   	 // 初始地址信息
			addressObj: {        // 地图选点信息
				longitude: '',
				latitude: '',
				address: '请选择集合地点'
			},
			controls: [           // 地图中心点图标, 可更换iconPath, 详情见官方文档关于map组件的介绍
				{
					iconPath: '/static/mi-map/position2.png',
					position: { 
						left: 175,
						top: 210,
						width: 30, 
						height: 30,
					},
					clickable: false
				}
			],
		};
	},
	mounted() {
		this.getLocation()
		this.initMapH()
	},
	methods:{
		// 查询现在的位置
		getLocation() {
			let this_ = this
			uni.getLocation({
				// type: 'gcj02', // 返回国测局坐标
				geocode: true,
				success: function(res) {
					this_.initMap(res)
					console.log(res);
				},
				fail: function(e) {
					uni.showToast({
						icon: 'none',
						title: '获取地址失败, 请检查是否开启定位权限~~'
					})
				}
			})
		},
		
		// 初始化我的位置
		async initMap(res) {
			this.longitude = res.longitude;
			this.latitude = res.latitude;
			this.myAddress = await this.getAddressName(res);
			
			this.addressObj = Object.assign({}, this.addressObj,{
				longitude: res.longitude,
				latitude: res.latitude,
				address: this.myAddress
			})
		},
		
		// 地图选择位置后 查询地点名称
		async checkMap(res) {
			this.addressObj = Object.assign({}, this.addressObj,{
				longitude: res.longitude,
				latitude: res.latitude,
				address: await this.getAddressName(res)
			})
			console.log('当前位置:' + res.latitude + '|' + res.longitude);
		},
		
		// 监听地图位置变化
		mapChange(e) {
			let that = this
			clearTimeout(this.timer)
			this.timer = setTimeout(() => {
				if (e.type == 'end') {
					that.mapCtx = uni.createMapContext('map', this)
					that.mapCtx.getCenterLocation({
						success: res => {
							this.checkMap(res)
						},
						fail: err => {
							console.log(err);
						}
					})
				}
			}, 200)
		},
		// 查询地图中心点的名称
		getAddressName(addressObj) {
			
			return new Promise((res) => {

















				

					// ======================== jsonp跨域 ======================== 
					const KEY = 'LXCBZ-NNIKD-UZ64F-H6AFI-UNJLH-OCFGE'
					let locationObj = addressObj.latitude+','+addressObj.longitude
					let url = 'https://apis.map.qq.com/ws/geocoder/v1?coord_type=5&get_poi=1&output=jsonp&poi_options=page_size=1;page_index=1';
					this.$jsonp(url,{
					  key: KEY,
					  location: locationObj
					}).then(e => {
						res(e.result.formatted_addresses.recommend);
					})
					.catch(err => {
						res(err);
					})

				
				
			})
			
		},
		// 计算地图的高度
		initMapH() {




				this.mapH = uni.getSystemInfoSync().windowHeight - 170;

		},
		// 移动到我的位置
		toMyLocation() {
			this.getLocation()
		},
		// 提交
		submitAdress() {
			this.controls = []
			setTimeout(() => {
				this.$emit('updateAddress', this.addressObj)
			}, 100)
		}
	},
}
